<template>
  <div class="leaderboard">
    <v-container>
      <h1 class="text-center white--text">
        Leaderboard-{{ this.$route.params.id }}
      </h1>
      <v-card
        max-width="600px"
        class="ma-auto mt-5 transparent resultcontainer"
      >
        <v-card-title>
          <v-text-field
            v-model="search"
            label="Search by Name/College"
            @keyup="searchResult"
            append-icon="mdi-magnify"
            class="ma-auto"
          />
        </v-card-title>
        <v-card-text>
          <v-list class="transparent" v-if="allresult.length > 0">
            <v-list-item
              v-for="(r, i) in positionedResult"
              :key="i"
              :class="getborderStyle(r.position)"
            >
              <v-list-item-avatar class="mb-5">
                <v-btn fab small dark :class="`${getStyle(r.position)}`">
                  <h2>{{ r.position }}</h2>
                </v-btn>
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title>
                  <b>{{ r.name }}</b>
                </v-list-item-title>
                <v-list-item-subtitle class="black--text">
                  {{ r.school }}
                </v-list-item-subtitle>
              </v-list-item-content>
              <v-list-item-action>
                <button class="totalmarks">{{ r.score }}</button>
              </v-list-item-action>
            </v-list-item>
          </v-list>
          <div v-else class="text-center">
            <v-progress-circular
              :size="100"
              class="ma-auto"
              color="primary"
              indeterminate
            ></v-progress-circular>
          </div>
        </v-card-text>
      </v-card>
    </v-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      search: "",
      testResults: [],
      allresult: [],
      results: [],
    };
  },

  methods: {
    modeltestResults() {
      fetch(
        `https://script.google.com/macros/s/AKfycbxHQvpCJacrv42u8RzaknFKc58rIQldmeY3zUB2EU4eOao0mUVk7ErwPN10r_MJREg4/exec?id=${this.$route.params.id}`
      )
        .then((res) => res.json())
        .then((data) => {
          fetch(data.exam.question)
            .then((res) => res.json())
            .then((d) => {
              let testResults = d;

              this.testResults = testResults
                .map((r) => ({
                  name: r.name,
                  school: r.school,
                  score: r.score,
                }))
                .sort((a, b) => b.score - a.score);
              this.allresult = this.testResults;
            });
        });
    },
    searchResult() {
      this.testResults = this.allresult.filter((r) => {
        if (
          r.school.toLowerCase().includes(this.search.toLowerCase()) ||
          r.name.toLowerCase().includes(this.search.toLowerCase())
        ) {
          return r;
        }
      });
    },
    getStyle(p) {
      if (p == 1) {
        return "success";
      } else if (p == 2) {
        return "orange darken-2";
      } else if (p == 3) {
        return "indigo";
      }
      return "";
    },
    getborderStyle(p) {
      if (p == 1) {
        return "s singleResult mb-2";
      } else if (p == 2) {
        return "f singleResult mb-2";
      } else if (p == 3) {
        return "d singleResult mb-2";
      }
      return "singleResult mb-2";
    },
  },
  computed: {
    positionedResult() {
      return this.testResults.map((r, i) => {
        return { ...r, position: i + 1 };
      });
    },
  },
  created() {
    this.modeltestResults();
    window.scrollTo(0, 0);
  },
};
</script>

<style lang="scss" scoped>
.leaderboard {
  background-attachment: fixed;
  min-height: 100vh;
  .resultcontainer {
    background: rgba(223, 221, 221, 0.747) !important;
    box-shadow: 0 8px 32px 0 rgba(12, 13, 32, 0.479);
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
    border-radius: 10px;
    border: 1px solid rgba(255, 255, 255, 0.18);
    .singleResult {
      background: rgba(255, 255, 255, 0.35);
      box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
      backdrop-filter: blur(18.5px);
      -webkit-backdrop-filter: blur(18.5px);
      border-radius: 10px;
      border: 1px solid rgba(255, 255, 255, 0.18);
    }
  }
}

.s {
  border: 2px solid rgba(22, 223, 49, 0.808) !important;
}
.f {
  border: 2px solid rgba(231, 154, 10, 0.808) !important;
}
.d {
  border: 2px solid rgba(23, 110, 192, 0.808) !important;
}
.totalmarks {
  border: 2px solid rgb(236, 25, 88) !important;
  height: 35px;
  width: 35px;
  padding: 3px;
  box-shadow: 2px 2px 4px rgba(236, 25, 88, 0.8);
}
</style>